<template>
    <div>
        <h1>文学部</h1>
        <h2>{{this.$route.query.userId}}</h2>
        <br>
        <ul>
            <li v-for="(x,index) in text">
                {{totalMarks(index)}}
                <span style="color: orangered;" @click="x.fold=true" v-if="!x.fold"> 收起</span>
                <span style="color: orangered;" @click="x.fold=false" v-if="x.fold"> 更多</span><br><br>
            </li>
        </ul>
        <!--<span>{{totalMarks}}</span>-->

        <div style="text-align: center;">
            <van-button type="primary" @click="toggle">主要按钮</van-button>
            <van-popup v-model="show" position="bottom" :overlay="false">
                <van-picker
                        show-toolbar
                        title=""
                        :columns="columns"
                        @cancel="onCancel"
                        @confirm="onConfirm">
                </van-picker>
            </van-popup>
            <van-swipe :autoplay="3000">
                <van-swipe-item v-for="image in images">
                    <img :src="image" width="100%"/>
                </van-swipe-item>
            </van-swipe>
        </div>
    </div>
</template>

<script>
    // function get (name) {
    //     var cookieName = encodeURIComponent(name) + "=",
    //         cookieStart = document.cookie.indexOf(cookieName),
    //         cookieValue = null;
    //     if (cookieStart > -1) {
    //         var cookieEnd = document.cookie.indexOf(";", cookieStart);
    //         if (cookieEnd == -1) {
    //             cookieEnd = document.cookie.length;
    //         }
    //         cookieValue = decodeURIComponent(document.cookie.substring(cookieStart + cookieName.length, cookieEnd))
    //     }
    //     return cookieValue;
    // }
    import Vue from 'vue'
    import {Toast} from 'vant';

    Vue.use(Toast);
    export default {
        name: "literature",
        data() {
            return {
                open:false,
                show: false,
                text: [
                    {title:"拥有财富 名声 势力 拥有整个世界的海贼哥尔多.D.罗杰 ",fold:true},
                    {title:"拥有财富 名声 势力 拥有整个世界的海贼哥尔多.D.罗杰 ",fold:true},
                    {title:"拥有财富 名声 势力 拥有整个世界的海贼哥尔多.D.罗杰" ,fold:true},
                ],
                images: [
                    'http://jns.img.bucket.ks3-cn-beijing.ksyun.com/skill/1/c9ee8e52-f47e-4990-8fe7-582bd94029a0.jpg',
                    'http://jns.img.bucket.ks3-cn-beijing.ksyun.com/skill/1/d8f238ae-358d-4d2c-a9c3-cbd081fa53bc.png',
                    'http://jns.img.bucket.ks3-cn-beijing.ksyun.com/skill/1/9c2b8d09-82ba-4e16-905d-ec7c7e9371ca.png',
                    'http://jns.img.bucket.ks3-cn-beijing.ksyun.com/skill/1/d4511b09-f892-49e0-b9d1-ed73a185ce2e.jpeg',
                    'http://jns.img.bucket.ks3-cn-beijing.ksyun.com/skill/1/0d9944f1-6aad-4894-b42e-72dadcfef058.png',
                ],
                columns: ['杭州', '宁波', '温州', '嘉兴', '湖州'],
                results: [{name: 'English', marks: 70}, {name: 'Math', marks: 80}, {name: 'History', marks: 90}],
            }
        },
        computed: {
            totalMarks: function () {
                return function (a) {
                    if (!this.text[a].title) return '';
                    if (this.text[a].title.length > 25 && this.text[a].fold === true) {
                        return this.text[a].title.slice(0, 25) + '...'
                    }
                    if (this.text[a].title.length > 25 && this.text[a].fold === false) {
                        return this.text[a].title
                    }
                }
            },
        },
        filters: {
            ellipsis: (value) => {
                if (!value) return '';
                if (value.length > 25) {
                    return value.slice(0, 25) + '...'
                }
                return value
            }
        },
        methods: {
            onConfirm(value, index) {
                Toast(`当前值：${value}, 当前索引：${index}`);
                this.show = !this.show
            },
            onCancel() {
                Toast('取消');
                this.show = !this.show
            },
            toggle() {
                this.show = !this.show
            },

        }
    }
</script>

